div.container-fluid.contactall {
  padding: 70px 15px 40px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #1d2b3f;
  color: white;
}

.contactall {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding: 0 0 5% 0;
}

.contactall-p {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 0 0 5px;
}

.getstarted-div {
  padding: 20px 10px;

  margin: 0 auto;
}
.getstarted {
  background-color: #3b5998;
  color: white;
  padding: 10px 20px;

  font-size: 18px;
  border-radius: 10px;
}
.text-h3 {
  font-weight: bold;
}
.faaa {
  padding: 5px;
  font-size: 5px;
  width: 30px;
  text-align: center;
  text-decoration: none;
  text-align: center;
  border-radius: 5px;
}
.fa:hover {
  opacity: 0.7;
}
.fa-linkedin {
  background-color: #007bb5;
}
.fa-facebook {
  background-color: #3b5998;
}
.fa-instagram {
  background-color: #881257;
}
.fa-twitter {
  background-color: #55acee;
}

@media (max-width: 768px) {
  div.container-fluid.contactall {
    padding: 20px 15px 0px;

    height: 100%;
  }
  .contactall {
    padding: 40px 0px 100px 0px;
    width: 100vw;
  }
  .contactall-p {
    padding: 15px;

    font-size: 20px;
  }
  .text-h3 {
    font-size: 20px;
  }
  .getstarted-div {
    padding: 20px 10px;
  }
  .getstarted {
    padding: 10px;

    font-size: 15px;
  }
}
