.side-drawer {
  height: 100%;

  background-color: transparent;
  background-image: linear-gradient(180deg, rgb(61, 128, 141), #242424);
  /* box-shadow: 1px 0px 7px rgb(87, 85, 85); */
  position: fixed;
  margin-top: 60px;
  top: 0;
  left: 0;
  width: 85%;
  max-width: 400px;
  z-index: 200;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /*justify-content: start; */
  transform: translateX(-100%);
  transition: transform 0.1s ease-out;
}
.side-drawer.open {
  transform: translateX(0);
}
.side-drawer_items {
  color: white;
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
  margin: 10px;
  padding-left: 30px;
}
.side-drawer_items:hover {
  color: red;
}

.side-drawer_items:focus {
  color: rgb(23, 126, 223);
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}
