.partnerall {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding: 0 0 5%;
  top: 0;
  left: 0;
  color: #032d69;
}

.partner {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0px;
}
.partner-h1 {
  padding: 40px 20px 20px 20px;

  font-weight: bold;
}
.partner-p {
  padding: 10px;

  font-size: 18px;
  font-weight: 400;
  max-width: 800px;
  margin: 0 auto;
}
.partner-img {
  text-align: center;
}
.mts-img,
.ers-img {
  width: 250px;
  height: 100px;
  margin: 15px;
}

@media (max-width: 768px) {
  .partnerall {
    padding: 0px 0px 40px 0px;
    width: 100vw;
  }

  .mts-img,
  .ers-img {
    width: 200px;
    height: 90px;
  }
  .partner-h1 {
    padding: 0px 10px;
    font-size: 18px;
  }
  .partner-p {
    padding: 0px 20px 10px;
    font-size: 16.2px;
  }
}
