.contact {
  /*background-color: rgba(0, 0, 0, 0.77);*/
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding: 4% 0 5% 0;
  color: #032d69;
  top: 0;
  left: 0;
  height: 100%;
}
.card-p {
  color: #032d69;
}
.contact-h1 {
  text-align: center;
  font-weight: bolder;
  font-size: 30px;
  padding-bottom: 30px;
}

.contact-p {
  align-items: center;
  max-width: 700px;
  margin: auto;
  text-align: left;
  font-size: 18px;
  padding-left: 5px;
}
.card-row {
  padding: 40px;
  margin: 0px;
}
.faaa {
  font-size: 20px;
  width: 30px;

  text-decoration: none;
  color: white;
  border-radius: 50%;
}
.fa-envelope,
.fa-globe,
.fa-phone {
  background-color: rgb(8, 52, 61);
}
.mylogo {
  text-align: center;
  padding: 5px;
}
.mylogo-img {
  width: 100px;
  height: 100px;
}

@media (max-width: 768px) {
  .contact-h1 {
    font-size: 20px;
    padding-bottom: 0px;
  }
  .contact {
    padding: 50px 0px 50px;
    width: 100vw;
  }
  .contact-p {
    font-size: 16.2px;
    padding: 15px 15px 15px 20px;
    margin: 0px;
  }
  .card-row {
    padding: 10px;
  }
  .tachyon {
    margin: 8px 4px;
  }

  .mylogo-img {
    width: 70px;
    height: 70px;
  }
}
