.service {
  height: 100%;
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  color: #032d69;
}

.service-header,
.process {
  font-size: 30px;
  padding: 20px 0px;
  font-weight: bolder;
  margin: 0px;
}

p {
  font-size: 18px;
}
.circle1,
.head1 {
  color: rgb(98, 179, 118);
}

@media (max-width: 768px) {
  .service-header,
  .process {
    font-size: 20px;
    padding: 20px 10px 15px;
  }

  .tachyon-service {
    margin: 4px 0px;
  }
  .tachyon-check {
    padding-top: 5px;
  }
  .headind-h3 {
    font-size: 18px;
  }

  p {
    font-size: 16.2px;
  }
  .service-header {
    padding-bottom: 0px;
  }
}
