.footer {
  background-color: #1d2b3f;
  color: white;
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  width: 100%;
  height: 70px;
  border-top: 1px solid #3b5998;
  /*position: fixed; */

  bottom: 0;
  left: 0;
}

.footer-content {
  text-align: center;
  font-size: 18px;

  padding-top: 10px;
}

@media (max-width: 769px) {
  .footer-content {
    font-size: 15px;
  }
}
