.im-size {
  height: 600px;
}
.mb-5 {
  padding: 10px;
  background-color: rgb(11, 75, 116);
  color: white;
  font-weight: bold;
  text-align: left;
}
.caption {
  margin-bottom: 15%;
}

.home-body {
  font-family: "Open Sans", sans-serif, Arial, Helvetica;
  text-align: center;
  justify-content: center;
  padding-top: 20px;
  font-weight: 400;
  color: #032d69;
}
.home-body-content {
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
}
.aboutus {
  max-width: 800px;
  margin: 0 auto;
  padding: 25px 20px 0px;
  font-size: 20px;
  text-align: left;
}

.home-body-h1,
.aboutus-h1 {
  text-align: center;

  font-weight: bold;
}
.home-body-p,
.aboutus-p {
  line-height: 1.9em;
  font-size: 18px;
  text-align: left;
  margin: 0px;
}
.circle2,
.head2 {
  color: rgb(100, 175, 100);
}

@media (max-width: 768px) {
  .im-size {
    height: 300px;
  }
  .mb-5 {
    padding: 10px;
    font-size: 15px;
    margin-bottom: 0px;
  }
  .caption {
    margin-bottom: 0px;
  }

  .home-body-content,
  .aboutus {
    padding: 5px 20px 0px;
  }
  .home-body-h1,
  .aboutus-h1 {
    font-size: 18px;
    text-align: center;
    padding: 10px;
  }
  .home-body-p,
  .aboutus-p {
    font-size: 16.2px;
  }
}
