.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button_line {
  width: 30px;
  height: 2px;
  background: black;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}
@media (max-width: 768px) {
  .toggle-button {
    display: flex;
  }
}
